:root {
  --main: #2c003e;
  --secondary: #721b65;
  --accent: #fe346e;
  --secondaryAccent: #d2fafb;
  --shadow: 0px 0px 15px rgba(0, 0, 0, 0.3);
  --background: lightgray;
}

* {
  box-sizing: border-box;
}
body {
  margin: 0;
  background-color: white;
}

nav {
  position: absolute;
  width: 95vw;
  z-index: 5;
  display: flex;
  justify-content: space-evenly;
  padding: 30px;
}
nav > a {
  color: var(--main);
  text-decoration: none;
  font-family: sans-serif;
  font-size: 1em;
}
nav > a:hover {
  color: var(--accent);
  cursor: pointer;
}

#my-name {
  position: absolute;
  width: 100%;
  z-index: 4;
  top: 20px;
  left: 0;
  transition: all fill 0.4s ease !important;
}

#base-header {
  background-color: var(--main);
  height: 100vh;
  width: 100%;
  border-bottom-right-radius: 0px;
}

#inner-header {
  box-shadow: var(--shadow);
  height: 90vh;
  width: 60%;
  background-color: white;
  border-bottom-right-radius: 300px;
  position: absolute;
}

#middle-header {
  height: 91vh;
  width: 67%;
  background-color: var(--secondary);
  position: absolute;
  border-bottom-right-radius: 300px;
}

#left-content {
  height: 100vh;
  width: 100%;
  border-bottom-right-radius: 100px;
  background-color: var(--main);
  padding: 4%;
  font-size: 1em;
}

#myPicture {
  margin-top: 20%;
  margin-bottom: 2%;
  margin-left: auto;
  margin-right: auto;
  border-bottom-right-radius: 100px;
  border-top-left-radius: 100px;
  display: block;
  height: 300px;
  width: 300px;
}

#about {
  color: white;
}

h1 {
  font-family: "Raleway", sans-serif;
  font-size: 3em;
  color: rgb(245, 208, 208);
}

#aboot {
  margin-top: 2%;
  margin-bottom: 4%;
}

#right-content {
  height: 80vh;
  width: 100%;
  background-color: white;
  border-top-left-radius: 150px;
  text-align: center;
}
.flex-wrapper {
  display: flex;
  flex-direction: column;
}
#filler {
  z-index: -1;
  position: absolute;
  background-color: var(--main);
  width: 100%;
  height: 40%;
}

#bar-wrapper {
  margin: 10px auto;
  display: flex;
  height: 30px;
  width: 70%;
}

#bar-heading {
  text-align: right;
  width: 20%;
  margin-right: 10px;
}

#bar {
  overflow: hidden;
  height: 30px;
  background-color: var(--accent);
  border-bottom-right-radius: 10px;
}

#projects {
  z-index: 100;
  height: 150vh;
  width: 90%;
  background-color: var(--secondary);
  margin: 5% auto;
  margin-bottom: 0;
  border-top-left-radius: 50px;
  border-bottom-right-radius: 50px;
  text-align: center;
  position: relative;
}

#cards-wrapper {
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
}

#cards-wrapper:hover {
  cursor: pointer;
}

#card {
  margin: 3% auto;
  width: 200px;
  height: 270px;
  z-index: 1;
  background-color: var(--secondary);
  outline: none;
  border: none;
}

#card-filler {
  background-color: lightgray;
  height: 100px;
  width: 200px;
  margin-top: -100px;
  z-index: -1;
}

#card-body {
  background-color: lightgray;
  width: 200px;
  height: 50px;
  border-bottom-right-radius: 90px;
  color: black;
}

#card-image {
  z-index: 1;
  height: 200px;
  width: 200px;
  border-bottom-right-radius: 80px;
  border-top-left-radius: 80px;
  margin: 0px;
}

#pop-up {
  text-align: center;
}
#pop-up-body {
  text-align: center;
  display: flex;
  flex-direction: column;
}

#pop-up-header {
  background-color: white;
  color: var(--main);
  margin: 5%;
}

#modal-picture {
  height: 200px;
  width: 200px;
  margin: 0 auto;
}

#footer {
  width: 95%;
  height: 300px;
  background-color: var(--main);
  margin-left: 1%;
  margin-top: -50px;
  padding-top: 70px;
  padding-bottom: 3%;
  text-align: center;
  z-index: -200;
  border-top-right-radius: 50px;
  border-bottom-left-radius: 50px;
}

#icon-wrapper {
  display: flex;
  justify-content: space-evenly;
}

.icon {
  color: white;
  font-size: 15px;
}

.icon a:link,
a:visited {
  color: white;
}
.icon a:hover {
  cursor: pointer;
  text-decoration: none;
  color: white;
  border-radius: 5px;
  background-color: #1e0727;
}

@media screen and (min-width: 700px) {
  nav > a {
    font-size: 20px;
  }
  #base-header {
    border-bottom-right-radius: 100px;
  }
  #inner-header {
    width: 60%;
  }
  #left-content {
    width: 40vw;
    border-bottom-right-radius: 200px;
  }
  #right-content {
    width: 60%;
  }

  .flex-wrapper {
    flex-direction: row;
  }
  #cards-wrapper {
    flex-direction: row;
  }

  h1 {
    font-size: 5em;
  }

  #card {
    width: 300px;
    height: 400px;
  }

  #card-image {
    height: 300px;
    width: 300px;
  }
  #card-body {
    width: 300px;
    height: 100px;
  }
  #card-filler {
    background-color: lightgray;
    height: 100px;
    width: 300px;
  }
  #projects {
    border-top-left-radius: 150px;
    border-bottom-right-radius: 150px;

    height: 90vh;
  }
  #modal-picture {
    height: 400px;
    width: 400px;
  }
  #footer {
    border-top-right-radius: 100px;
    border-bottom-left-radius: 100px;
  }
  .icon {
    font-size: 20px;
  }
}
